import React from "react";
import "./Jumbotron.css";

const Hero = props =>
  <div
    className="jumbotron text-center"
  >
    <h1>NYT Search and Save</h1>
    <h2>Search for and curate articles from the New York Times</h2>
  </div>;

export default Hero;



